export default function(state = initialState, action) {
  switch (action.type) {
    case ACTION_TYPE.FETCH_EXCHANGE_LIST:
      return {
        ...state,
        items: action.payload
      };
    case ACTION_TYPE.SELECT_PAIR:
      return {
        ...state,
        selected_pair: action.payload
      };
    case ACTION_TYPE.SET_TICKER_DATA:
      return {
        ...state,
        ticker_data: action.payload
      };
    default:
      return state;
  }
}